<template>
  <div id="login">
    <div class="loginBox">
      <h2>登录</h2>
      <div class="inputBox">
        <input v-model="username" placeholder="用户名" id="username"/>
      </div>
      <div class="inputBox">
        <input v-model="password" type="password" placeholder="密码" @keydown.enter="onLogin"/>
      </div>
      <el-button size="medium" @click="onLogin">登录</el-button>
      <p class="notice">
        没有账号？
        <router-link to="/register">注册新用户</router-link>
      </p>
    </div>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
export default {
  data(){
    return {
      username: '',
      password: '',
    }
  },
  methods:{
    ...mapActions(['login']),
    onLogin(){
      this.login({username: this.username, password: this.password})
        .then(()=>{
          this.$router.push({path: this.$route.query.redirect || '/'})
        })
    }
  }
};
</script>

